import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import FluxApi from '@/components/FluxApi'
import Notice from '@/components/Notice'

Vue.use(Router)

export default new Router({
	mode: 'history',
	  routes: [
	  {
	      path: '/',
	      name: 'Notice',
	      component: Notice
	    },
	    {
	      path: '/api',
	      name: 'Notice',
	      component: Notice
	    },
	    {
	      path: '/api/tools',
	      name: 'HelloWorld',
	      component: HelloWorld
	    },
	    {
	      path: '/api/flux',
	      name: 'FluxApi',
	      component: FluxApi
	    },
	  ]
})



