<template>
  <div class="app">
    <nav class="mynav">
      <router-link @click.native='cc(3)' style="color: white" to="/api">API调用</router-link>
      <img src="./assets/logo45.png">
    </nav>
    <div id='navrbdid' class="navrbd">
      <img src="./assets/diaoyong.png">
      <router-link :class="num1?'flsakfjl':''" @click.native='cc(1)' to="/api/tools">淘宝工具箱任务</router-link>
      <router-link :class="num2?'flsakfjl':''" @click.native='cc(2)' to="/api/flux">淘宝流量任务</router-link>
    </div>

    <i @click="stotop" id='scrollup' class="el-icon-arrow-up arrow_top" alt="返回顶部" title="返回顶部"></i>
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
export default {
    name: 'App',
    data: function() {
        return {
            num1: false,
            num2: false
        }
    },
    methods: {
        cc(s) {
          switch(s){
            case 1:
                this.num1=true;
                this.num2=false;
                break;
            case 2:
                this.num1=false;
                this.num2=true;
                break;
            case 3:
                this.num1=false;
                this.num2=false;
                break;
          }
        },
        stotop() {
            console.log(123)
            window.scrollTo(0,0)
        }
    }
}
window.onscroll= function(){
    //变量t是滚动条滚动时，距离顶部的距离
    var t = document.documentElement.scrollTop||document.body.scrollTop;
    var scrollup = document.getElementById('scrollup');
    var navrbdid = document.getElementById('navrbdid');
    //当滚动到距离顶部200px时，返回顶部的锚点显示
    if(t>=2000){
        scrollup.style.display="block";
        navrbdid.style.borderBottom="1px solid #ccc";
    }else{          //恢复正常
        scrollup.style.display="none";
        navrbdid.style.borderBottom="none";
    }
}

</script>

<style scoped>
.app {
  
}
.mynav {
  background-color: rgb(43,48,59);
  color: white;
  /*padding: 15px;*/
  display: flex;
  align-items: center;
  height: 40px;
  padding-left: 20px;
  position: fixed;
  width: 100%;
  z-index: 999;
}
.mynav img {
  height: 22px;
  margin-left: 15px;
}
.navrbd {
    background-color: #fff;
    -webkit-box-shadow: 0 2px 2px 0 rgba(207,216,221,.7);
    -moz-box-shadow: 0 2px 2px 0 rgba(207,216,221,.7);
    box-shadow: 0 2px 2px 0 rgba(207,216,221,.7);
    border-bottom: none;
    height: 50px;
    display: flex;
    align-items: center;
    position: fixed;
    padding-left: 20px;
    width: 100%;
    z-index: 999;
    top: 40px;
    font: 16px/1.5 '\5b8b\4f53',sans-serif;
}
.navrbd img {
  height: 22px;
  padding-right: 30px;
}
.flsakfjl {
  color: #00a4ff
}
a {
  color: #000;
  text-decoration: none;
  padding-right: 20px;
}
a:hover {
  color: #00a4ff
}
    .arrow_top {
        position: fixed;
        z-index: 999999;
        right: 8px;
        bottom: 15px;
        /*width: 44px;
        height: 44px;*/
        padding: 15px;
        box-sizing: border-box;
        white-space: nowrap;
        /*text-indent: 100%;*/
        overflow: hidden;
        border-radius: 50%;
        border: 1px solid #e1e8f0;
        background-color: #fff;
        box-shadow: 0 0 0 1px rgba(227,229,233,.25);
        cursor: pointer;
        display: none;
    }
</style>



